  &__categories-link {\n    font-weight: normal;\n    font-size: 15px;\n    line-height: 18px;\n    color: $primary;\n    opacity: 0.5;\n    margin-right: 10px;\n\n    &:last-child {\n      margin-right: 0;\n    }\n\n    @media(max-width: 767px) {\n      font-size: 14px;\n      margin-right: 5px;\n    }\n  }\n\n  &__code {\n    font-weight: 400;\n    font-size: 13px;\n    color: $dark;\n    margin: 20px 0;\n\n    @media(max-width: 767px) {\n      margin: 15px 0;\n    }\n  }\n\n  &__entry {\n    width: 100%;\n    max-width: 100%;\n\n    &--desktop {\n      @media(max-width: 1740px) {\n        display: none;\n      }\n    }\n\n    &--mobile {\n      display: none;\n      @media(max-width: 1740px) {\n        display: block;\n      }\n    }\n  }\n\n  &__entry-title {\n    color: $dark;\n    font-weight: 600;\n    font-size: 20px;\n    margin-bottom: 30px;\n\n    @media(max-width: 767px) {\n      font-size: 18px;\n      margin-bottom: 20px;\n    }\n  }\n\n  &__entry-descr , &__entry-descr p {\n    font-weight: normal;\n    font-size: 16px;\n    color: $dark2;\n    line-height: 1.7;\n\n    p {\n      margin-bottom: 15px;\n    }\n\n    @media(max-width: 767px) {\n      font-size: 14px;\n    }\n  }\n\n  &__bottom {\n    width: 100%;\n    max-width: 100%;\n    @media(max-width: 1440px) {\n      order: 3;\n    }\n  }\n\n  &__order {\n    display: none;\n    margin: 30px 0;\n\n    @media(max-width: 991px) {\n      display: block;\n    }\n  }\n\n  &__order-row {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n  }\n\n  &__folders {\n    margin-bottom: 30px;\n\n    @media(max-width: 767px) {\n      margin-bottom: 20px;\n    }\n\n    &:last-child {\n      margin-bottom: 0;\n    }\n  }\n\n  &__folders-title {\n    color: $dark;\n    font-weight: 400;\n    font-size: 22px;\n    margin-bottom: 20px;\n\n    @media(max-width: 767px) {\n      font-size: 18px;\n      margin-bottom: 15px;\n    }\n  }\n\n  &__folders-list {\n    display: flex;\n    flex-wrap: wrap;\n    align-items: center;\n  }\n\n  &__folders-link {\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    font-size: 14px;\n    font-weight: 500;\n    line-height: 1.2;\n    color: #fff;\n    background: $dark;\n    box-shadow: 7px 7px 30px $shadow-primary;\n    margin: 0 10px 10px 0;\n    padding: 10px 20px 9px;\n    border-radius: 10px;\n\n    @media(max-width: 767px) {\n      padding: 5px 10px;\n      border-radius: 5px;\n      margin: 0 5px 5px 0;\n      font-size: 13px;\n    }\n  }\n\n  &__form {\n    width: 100%;\n    max-width: 405px;\n    padding: 50px 50px 50px;\n    box-shadow: 7px 7px 30px $shadow-primary;\n    background: #FFF;\n    width: 100%;\n    border-radius: 20px;\n\n    @media(max-width: 1199px) {\n      max-width: 350px;\n      padding: 40px 30px 30px;\n    }\n\n    @media(max-width: 991px) {\n      display: none;\n    }\n  }\n\n  &__form-title {\n    color: $dark;\n    font-weight: 700;\n    font-size: 30px;\n    margin-bottom: 20px;\n\n    @media(max-width: 1199px) {\n      font-size: 25px;\n      margin-bottom: 15px;\n    }\n  }\n\n  &__form-tag {\n    background: rgba(41, 194, 158, 0.15);\n    border-radius: 7px;\n    font-weight: 500;\n    font-size: 13px;\n    color: #29c29e;\n    padding: 7px 14px 7px 37px;\n    display: inline-flex;\n    position: relative;\n    margin-bottom: 30px;\n    @media(max-width: 1199px) {\n      margin-bottom: 20px;\n    }\n  }\n\n  &__form-tag-ico {\n    color: #29C29E;\n    width: 12px;\n    height: 12px;\n    position: absolute;\n    left: 14px;\n    top: calc(50% - 6px);\n  }\n\n  &__price {\n    width: 100%;\n    display: flex;\n    flex-wrap: wrap;\n    align-items: center;\n    margin: 20px 0;\n\n    @media(max-width: 991px) {\n      margin: 0;\n      height: 36px;\n    }\n\n    @media(max-width: 410px) {\n      height: auto;\n    }\n  }\n\n  &__current-price {\n    font-size: 24px;\n    color: $primary;\n    margin-right: 20px;\n\n    #{$root} {\n      &__price-label {\n        font-size: 20px;\n      }\n    }\n\n    @media(max-width: 1199px) {\n      font-size: 22px;\n    }\n\n    @media(max-width: 575px) {\n      font-size: 18px;\n      margin-right: 15px;\n    }\n  }\n\n  &__price-val {\n    font-weight: bold;\n    margin-right: 5px;\n  }\n\n  &__old-price {\n    font-size: 20px;\n    color: $primary;\n    opacity: .2;\n    text-decoration: line-through;\n    #{$root} {\n      &__price-val {\n        font-weight: 500;\n      }\n      &__price-label {\n        font-size: 18px;\n\n        @media(max-width: 575px) {\n          font-size: 14px;\n        }\n      }\n    }\n\n    @media(max-width: 1199px) {\n      font-size: 18px;\n    }\n\n    @media(max-width: 575px) {\n      font-size: 16px;\n    }\n  }\n\n  &__add {\n    margin-top: 30px;\n    width: 100%;\n    display: inline-flex;\n    justify-content: space-between;\n    align-items: center;\n    font-weight: 500;\n    font-size: 15px;\n    line-height: 1.2;\n    padding: 20px 30px;\n    color: #fff;\n    border: none;\n    background: $dark;\n    box-shadow: 7px 7px 30px $shadow-primary;\n    transition: all 0.18s linear;\n    border-radius: 10px;\n\n    &:hover, &:focus {\n      background: $dark2;\n    }\n\n    @media(max-width: 991px) {\n      max-width: 300px;\n    }\n  }\n\n  &__add-ico {\n    fill: #FFF;\n    width: 21px;\n    height: 21px;\n  }\n}\n","$primary: #191919;\n$dark: #41445C;\n$dark2: #53566c;\n$shadow-primary: rgba(65, 68, 92, 0.3);\n$shadow-dark2: rgba(248, 248, 248, 0.2);\n$grey: #818181;\n$light: #c4c4c4;"],"sourceRoot":""}]);
